import { Component, OnInit } from '@angular/core';
import { HomeService } from 'src/app/core/services/home.service';
import { Content } from 'src/app/models/content';

declare function initCustomScriptForAngular():any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {
  homeContents!:Content[];
  constructor(private homeService: HomeService) { }
  ngOnInit(): void {
    //initialize the animation placed in the script files.
    initCustomScriptForAngular();

    this.populateSliderContent();


  }
  populateSliderContent(){
    this.homeService.getAllHomeContent().subscribe(res=>{
     this.homeContents = res;
     console.log("this.homeContent: ", this.homeContents);
    });
  }

}
